import React from 'react';
import ReactDOM from 'react-dom';

var x=0;
var flag=0;
var sum=0;
class Cal extends React.Component { 
	op= a =>{
		x=(x*10)+a;		
		document.getElementById('show').innerHTML=x;		
	}
	add=()=>{
		if(flag===0){
			sum=x;
		}
		else if(flag===1){
			sum=sum+x;
		}
		else if(flag===2){
			sum=sum-x;
		}
		else if(flag===3){
			sum=sum*x;
		}
		else if(flag===4){
			sum=sum/x;
		}
		document.getElementById('show').innerHTML=sum;
		flag=1;
		x=0;
	}
	sub=()=>{
		if(flag===0){
			sum=x;
		}
		if(flag===1){
			sum=sum+x;
		}
		else if(flag===2){
			sum=sum-x;
		}
		else if(flag===3){
			sum=sum*x;
		}
		else if(flag===4){
			sum=sum/x;
		}
		document.getElementById('show').innerHTML=sum;
		flag=2;
		x=0;
	}
	mul=()=>{
		if(flag===0){
			sum=x;
		}
		if(flag===1){
			sum=sum+x;
		}
		else if(flag===2){
			sum=sum-x;
		}
		else if(flag===3){
			sum=sum*x;
		}
		else if(flag===4){
			sum=sum/x;
		}
		document.getElementById('show').innerHTML=sum;
		flag=3;
		x=0;
	}
	clear=()=>{
		x=0;
		flag=0;
		sum=0;
		document.getElementById('show').innerHTML=x;
	}
	equals=()=>{
		if(flag===0){
			sum=x;
		}
		if(flag===1){
			sum=sum+x;
		}
		else if(flag===2){
			sum=sum-x;
		}
		else if(flag===3){
			sum=sum*x;
		}
		else if(flag===4){
			sum=sum/x;
		}
		document.getElementById('show').innerHTML=sum;
				
	}
	divide=()=>{
		if(flag===0){
			sum=x;
		}
		if(flag===1){
			sum=sum+x;
		}
		else if(flag===2){
			sum=sum-x;
		}
		else if(flag===3){
			sum=sum*x;
		}
		else if(flag===4){
			sum=sum/x;
		}
		document.getElementById('show').innerHTML=sum;
		flag=4;
		x=0;
	}
	
	sign=()=>{
		if(flag===0){
		sum=x;
		}
		sum=-sum;
		document.getElementById('show').innerHTML=sum;
		flag=5;
	}
	square=()=>{
		if(flag===0){
		sum=x;
		}
		sum=sum*sum;
		document.getElementById('show').innerHTML=sum;
		flag=5;
	}
	root=()=>{
		if(flag===0){
		sum=x;
		}
		sum=Math.sqrt(sum)
		document.getElementById('show').innerHTML=sum;
		flag=5;
	}
	
	sci=()=>{
		var m=document.getElementById('showsci');
		if(m.style.display==='none'){
			m.style.display='block';
		}
	}
	constructor(props){
		super(props);
		this.state={
			color: '#000',
			backgroundColor: '#f0f0f0',
			fontSize: 24,
			width: '180px',
		};
		document.body.style.backgroundColor='#fff';
		
	}
	
	light=()=>{
		this.setState({
			color: '#000',
			backgroundColor: '#f0f0f0'
		});
		document.body.style.backgroundColor='#fff';
	}
	dark=()=>{
		this.setState({
			color: '#fff',
			backgroundColor: '#666'
		});
		document.body.style.backgroundColor='#000';
	}
	
	render(){
		const mystyle={
			backgroundColor: '#fff',
			border: '1px solid',
			width: '710px',
			paddingRight: '10px',
			textAlign: 'right',
			fontSize: 30
		};
		return (<div align='center'>
		<p style={mystyle} id='show'>0</p>
 		<button style={this.state} onClick={() =>this.op(1)}>1</button>
		<button style={this.state} onClick={() =>this.op(2)}>2</button>
		<button style={this.state} onClick={() =>this.op(3)}>3</button>
		<button style={this.state} onClick={this.add}>Add (+)</button><br/>
		<button style={this.state} onClick={() =>this.op(4)}>4</button>
		<button style={this.state} onClick={() =>this.op(5)}>5</button>
		<button style={this.state} onClick={() =>this.op(6)}>6</button>
		<button style={this.state} onClick={this.sub}>Subtract (-)</button><br/>
		<button style={this.state} onClick={() =>this.op(7)}>7</button>
		<button style={this.state} onClick={() =>this.op(8)}>8</button>
		<button style={this.state} onClick={() =>this.op(9)}>9</button>
		<button style={this.state} onClick={this.mul}>Multiply (*)</button><br/>
		<button style={this.state} onClick={this.clear}>Clear</button>
		<button style={this.state} onClick={() =>this.op(0)}>0</button>
		<button style={this.state} onClick={this.equals}>=</button>
		<button style={this.state} onClick={this.divide}>Divide (/)</button><br/>
		<button style={this.state} onClick={this.sci}>Scientfic Mode</button>
		<button style={this.state} onClick={this.light}>Light Theme</button>
		<button style={this.state} onClick={this.dark}>Dark Theme</button>
		<div id='showsci'>
		<button style={this.state} onClick={this.sign}>Sign (+/-)</button>
		<button style={this.state} onClick={this.square}>Square</button>
		<button style={this.state} onClick={this.root}>Square Root</button>
		</div>
		</div>);
	}
}
window.onload=function(){
  document.getElementById('showsci').style.display='none';
}
ReactDOM.render(<Cal />,document.getElementById('root'));
